import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { NgxPermissionsGuard } from "ngx-permissions";
import { CoreComponent } from './core.component';

const routes: Routes = [
  {
    path: '',
    component: CoreComponent,
    children: [
      {
        path: '',
        loadChildren: '../components/home/home.module#HomeModule'
      },
      {
        path:'usuarios',
        canActivateChild: [NgxPermissionsGuard],
        loadChildren: '../components/users/users.module#UsersModule',
        data: {
          title: 'Usuarios',
          permissions: {
            only: 'USUARIOS',
            redirectTo: '/'
          }
        }
      },
      {
        path:'usuariosA',
        canActivateChild: [NgxPermissionsGuard],
        loadChildren: '../components/usuarios/usuarios.module#UsuariosModule',
        data: {
          title: 'Usuarios App',
          permissions: {
            only: 'USUARIOSA',
            redirectTo: '/'
          }
        }
      },
      {
        path:'carpetas',
        canActivateChild: [NgxPermissionsGuard],
        loadChildren: '../components/carpetas/carpetas.module#CarpetasModule',
        data: {
          title: 'Carpetas de investigación',
          permissions: {
            only: 'CARPETAS',
            redirectTo: '/'
          }
        }
      },
      {
        path:'indicios',
        canActivateChild: [NgxPermissionsGuard],
        loadChildren: '../components/indicios/indicios.module#IndiciosModule',
        data: {
          title: 'Indicios',
          permissions: {
            only: 'INDICIOS',
            redirectTo: '/'
          }
        }
      },
      {
        path:'peritos',
        canActivateChild: [NgxPermissionsGuard],
        loadChildren: '../components/peritos/peritos.module#PeritosModule',
        data: {
          title: 'Indicios',
          permissions: {
            only: 'PERITOS',
            redirectTo: '/'
          }
        }
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CoreRoutingModule { }